<script>
    import { getContext } from "svelte";

    import { appLang } from '../../constants';
    import { similarityStore } from "./similarityStore.js";
    const { selectedRegions } = similarityStore;
    import SimilarRegion from "./SimilarRegion.svelte";

    export let qImg;
    export let sImgsPromise;

    const similarityPropagatedContext = getContext("similarityPropagatedContext") || false;  // true if it's a suggestion, false otherwise
</script>

{#await sImgsPromise}
    <div class="faded is-center">
        { appLang === 'en' && !similarityPropagatedContext
        ? 'Retrieving similar regions...'
        : appLang === 'fr' && !similarityPropagatedContext
        ? 'Rcupration des rgions similaires...'
        : appLang === 'en' && similarityPropagatedContext
        ? "Retrieving propagated regions..."
        : "Rcupration de similarits propages..."
        }
    </div>
{:then simImgs}
    {#each simImgs as [score, _, sImg, qRegions, sRegions, category, users, isManual]}
        <SimilarRegion qImg={qImg} sImg={sImg} score={score} qRegions={qRegions} sRegions={sRegions} category={category} users={users} isManual={isManual}/>
    {:else}
        {#if Object.values($selectedRegions).length === 0}
            <div class="faded is-center">
                {appLang === 'en' ? 'No document selected' : 'Aucun document slectionn'}
            </div>
        {:else}
            <div class="faded is-center">
                {appLang === 'en' ? 'No similar regions' : 'Pas de rgions similaires'}
            </div>
        {/if}
    {/each}
{:catch error}
    <div class="faded is-center">
        {
            appLang === 'en' ?
            `Error when retrieving similar regions: ${error}` :
            `Erreur de recupration des rgions similaires: ${error}`
        }
    </div>
{/await}
